<template>
	<div>
		<!-- Cabecera de Estados y Fecha Inicio y Fin -->
		<div class="vx-row" style="padding-bottom:25px">
			<!-- Cabecera de Estado -->
			<div class="vx-col w-full md:w-1/2 lg:w-1/2 xl:w-1/2">
				<span class="inline-flex relative">
					<div style="padding-right:20px">
						<vx-card class="">
							<p>Estado</p>
						</vx-card>
					</div>
					<vx-card style="height:63px; width:196px">
						<v-select style="margin-top:-6px" :options="roleOptions" :clearable="false" v-model="filtros.estado" class="mb-4 md:mb-0" />
					</vx-card>
				</span>
			</div>
			<!-- Cabecera de Fechas -->
			<div class="vx-col w-full md:w-1/2 lg:w-1/2 xl:w-1/2">
				<span class="inline-flex relative">
					<div style="padding-right:20px;">
						<vx-card style="width: 151px;">
							<p>Fecha de Inicio</p>
						</vx-card>
					</div>
					<div style="padding-right:20px;">
						<vx-card style="height:63px">
							<span class="inline-flex relative">
								<IconifyIcon  :icon="calendarIcon" width="20" />
								<flat-pickr style="padding-left:15px; margin-top:-6px" :config="configFechaInicio" v-model="filtros.fechaInicio" @on-change="onFromChange" />
							</span>
						</vx-card>
					</div>
					<div style="padding-right:20px;">
						<vx-card style="height:63px">
							<span class="inline-flex relative">
								<IconifyIcon  :icon="calendarIcon" width="20" />
								<flat-pickr style="padding-left:15px; margin-top:-6px" :config="configFechaFin" v-model="filtros.fechaFin" @on-change="onToChange" />
							</span>
						</vx-card>
					</div>
				</span>
			</div>
		</div>
		<!-- Tabla de Listado de Campañas -->
		<div class="vx-row">
			<div class="vx-col w-full">
				<tabla-listado></tabla-listado>				
			</div>
		</div>
	</div>
</template>


<script>
import vSelect from 'vue-select'
import flatPickr from 'vue-flatpickr-component';
import 'flatpickr/dist/flatpickr.css';
import TablaListado from './TablaListado.vue';
import IconifyIcon from '@iconify/vue';
import calendarIcon from '@iconify/icons-bi/calendar-week';

export default {
	components:{
		vSelect,
		flatPickr,
		TablaListado,
		IconifyIcon,
	},
	data(){
		return{
			calendarIcon,
			filtros:{
				fechaInicio: new Date(),
				fechaFin: new Date(),
				estado: { label: 'Activas', value: 'activas' },
			},
			configFechaInicio: {
				minDate: null,
				maxDate: null,
				dateFormat: 'd-m-Y',
            },
            configFechaFin: {
				minDate: new Date(),
				dateFormat: 'd-m-Y',
            },
			roleOptions: [
				{ label: 'Activas', value: 'activas' },
				{ label: 'Programadas', value: 'programadas' },
				{ label: 'Finalizadas', value: 'finalizadas' },
			],
		}
	},
	methods:{
		onFromChange(selectedDates, dateStr, instance) {
			this.$set(this.configFechaFin, 'minDate', dateStr);
		},
		onToChange(selectedDates, dateStr, instance) {
			this.$set(this.configFechaInicio, 'maxDate', dateStr);
		},
	},
}
</script>


<style scoped>
.flatpickr-input{
    width: 96px;
	border: 0px;
}
</style>